.tabs-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.tab-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100px;
}

.tab-item div {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.tab-item .item-img {
    height: 40px;
    width: 40px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

path {
    fill: rgb(226, 21, 21);
}

.tab-item p {
    font-size: 12px;
    color: #3a3a3a;
    text-align: center;
    line-height: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
}

.tab-item .selected-container p {
    color: #d65db1;
}

.tab-item .selected-container .selector {
    /* position: absolute; */
    bottom: 0px;
    width: 40px;
    height: 3px;
    background-image: -webkit-gradient(linear, left bottom, right top, from(#845ec2), color-stop(#d65db1), to(#ffc75f));
    background-image: -webkit-linear-gradient(left bottom, #845ec2, #d65db1, #ffc75f);
    background-image: linear-gradient(to right top, #845ec2, #d65db1, #ffc75f);
}


/*PRODUCT */

.product-list {
    max-width: 700px;
    padding: 0 10px;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.expired {
    position: absolute;
    top: -10px;
    right: 20px;
    background: #d83d3d;
    color: #fff;
    border-radius: 2px;
    padding: 2px 3px;
    font-size: 12px;
}

.product-list article,
.product-list article.opened {
    background: #fff;
    border: 1px solid #bbb;
    border-radius: 5px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
    width: 97%;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    height: 102px;
    position: relative;
}

.product-list article.opened {
    border: 1px solid #d65db1;
    height: 220px;
}

.product-content {
    display: flex;
}

.product-list article .product-data {
    margin-left: 10px;
    margin-right: 10px;
    width: 100%;
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.product-list article .product-img {
    width: 120px;
    height: 80px;
    background-image: url('https://images-americanas.b2w.io/produtos/01/00/offers/01/00/item/133274/7/133274700_1GG.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.product-list article .product-data strong {
    font-size: 15px;
    color: #333;
    font-weight: 700;
    line-height: 20px;
}

.product-list article .product-data p {
    font-size: 14px;
    font-weight: 500;
    color: #6b6b6b;
    line-height: 20px;
    white-space: nowrap;
    width: 180px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.product-list article .product-data p strong {
    font-size: 16px;
    color: #845ec2;
    line-height: 20px;
    font-weight: 900;
}

.product-list article .product-data a {
    height: 35px;
    border-radius: 5px;
    border: 2px solid #da255f;
    background: none;
    /* margin-top: 5px; */
    color: #da255f;
    font-weight: bold;
    font-size: 16px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.product-list article .product-type {
    width: 35px;
    height: 35px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.product-list article .product-type .type-icon {
    height: 20px;
    width: 20px;
    background-image: url(/static/media/coupon.33ce38d1.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.product-list article .product-type p {
    font-size: 9px;
    color: #d65db1;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
}


/* CARD DETAILS */

.product-more-info {
    height: 90px;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    position: relative;
}

.desc-container p {
    font-size: 12px;
    font-weight: #555;
    height: 30px;
    margin-bottom: 3px;
}

.coupon-container {
    display: flex;
    flex-direction: column;
}

.coupon-container .tips {
    display: flex;
    align-items: center;
    justify-content: center;
}

.product-more-info .coupon-container .tips .touch-icon {
    height: 13px;
    width: 13px;
    background-image: url(/static/media/touch.0ced22fc.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.product-more-info .coupon-container .tips p {
    font-size: 13px;
    text-align: center;
}

.product-more-info .coupon-container .coupon {
    padding: 6px 20px;
    border: 1.5px dashed #d65db1;
    border-radius: 3px;
    font-size: 14px;
    font-weight: 600;
    text-align: center;
    color: #d65db1;
    font-family: 'Lato', sans-serif;
}

.promo-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 19px;
}

.product-more-info .promo-container p {
    padding: 6px 20px;
    font-size: 14px;
    font-weight: 600;
    text-align: center;
    font-family: 'Lato', sans-serif;
}

.copy-toast {
    position: absolute;
    left: calc(50% - 37.5px);
    width: 75px;
    background-color: #d65db1;
    border-radius: 5px;
    padding: 3px 5px;
    color: #fff;
    -webkit-animation-name: example;
    -webkit-animation-duration: 0.4s;
    -webkit-animation-fill-mode: forwards;
    animation-name: example;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
    text-align: center;
}

@-webkit-keyframes example {
    from {
        top: 15px;
        visibility: visible;
    }
    to {
        top: 5px;
        background-color: #d65db29a;
        visibility: hidden;
    }
}

@keyframes example {
    from {
        top: 15px;
        visibility: visible;
    }
    to {
        top: 5px;
        background-color: #d65db29a;
        visibility: hidden;
    }
}

.btn-url {
    text-decoration: none;
    text-align: center;
    margin-top: 3px;
    padding: 5px 10px;
    background-image: -webkit-gradient(linear, left bottom, right top, from(#d65db1), color-stop(#ff6791), to(#ffc75f));
    background-image: -webkit-linear-gradient(left bottom, #d65db1, #ff6791, #ffc75f);
    background-image: linear-gradient(to right top, #d65db1, #ff6791, #ffc75f);
    border: none;
    color: #fff;
    font-size: 15px;
    font-weight: 500;
    border-radius: 4px;
    cursor: pointer !important;
}

.navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    margin-top: -15px;
}

.navigation button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px 10px;
    background-color: #da255f;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    color: #fff;
}

.navigation p {
    color: rgb(78, 78, 78);
    font-size: 13px;
    font-weight: 500;
}

.loading-image {
    height: 100%;
    width: 120px;
    background-image: url(/static/media/wait.1d7626e6.gif);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.request-error {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.request-error h1 {
    font-size: 18px;
    margin: 5px auto;
}

.request-error p {
    font-size: 14px;
    padding: 0 5px;
    text-align: center;
    color: #888;
}

.request-error button {
    margin-top: 10px;
    padding: 15px 20px;
    background-image: -webkit-gradient(linear, left bottom, right top, from(#845ec2), color-stop(#ff6791), to(#ffc75f));
    background-image: -webkit-linear-gradient(left bottom, #845ec2, #ff6791, #ffc75f);
    background-image: linear-gradient(to right top, #845ec2, #ff6791, #ffc75f);
    border: none;
    color: #fff;
    font-weight: 600;
    border-radius: 50px;
    cursor: pointer !important;
}

@media only screen and (min-width: 550px) {
    .product-list {
        max-width: 100%;
    }
    .product-list article {
        max-width: 500px;
    }
}

@-webkit-keyframes fadein {
    from {
        bottom: 0;
        opacity: 0;
    }
    to {
        bottom: 30px;
        opacity: 1;
    }
}

@keyframes fadein {
    from {
        bottom: 0;
        opacity: 0;
    }
    to {
        bottom: 30px;
        opacity: 1;
    }
}

@-webkit-keyframes fadeout {
    from {
        bottom: 30px;
        opacity: 1;
    }
    to {
        bottom: 0;
        opacity: 0;
    }
}

@keyframes fadeout {
    from {
        bottom: 30px;
        opacity: 1;
    }
    to {
        bottom: 0;
        opacity: 0;
    }
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body,
html {
    font-family: 'Lato', sans-serif;
    background: #fff;
    color: #333;
    text-rendering: optimizeSpeed !important;
    text-rendering: geometricPrecision !important;
    text-rendering: optimizeLegibility !important;
    -webkit-font-smoothing: antialiased !important;
}
