.header-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px;
}

.header-container .logo {
    height: 100px;
    width: 100px;
    background-image: url('../../assets/logo.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    border: 1px solid #ccc;
    border-radius: 50%;
}

.header-container .name {
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-container .name p {
    margin-top: 3px;
    font-size: 18px;
    color: #444;
    font-weight: 800;
    line-height: 20px;
}

.header-container .instagram-checked {
    height: 17px;
    width: 17px;
    background-image: url('../../assets/checked.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.header-container p {
    margin-top: 3px;
    font-size: 12px;
    color: rgb(117, 117, 117);
    font-weight: 900px;
    line-height: 15px;
}